import {mapState} from "vuex"
export default {
	computed:{
		...mapState([
			'userMsgs'
		])
	},
	data() {
		return{
			list:[],
		}
	},
	methods:{
		// 进入详情页
		gotoDetail(e) {
			// console.log(e);
			uni.navigateTo({
				url: '../productDetail/productDetail?goodsid='+e,
			});
		}
	},
	async onLoad() {
		var that = this
		uni.showLoading({
			title: '加载中',
			mask: true
		});
		await that.$http.lookGoodsIsSaved({
			uid:that.userMsgs.id
		}).then(res => {
			var list = res.data
			list.forEach(function(item) {
				that.$http.lookGoodsDetailMsgs({
					gid:item.goodsid
				}).then(res => {
					item.books = res.data
					that.list.push(item)
				})
			})
			uni.hideLoading()
			console.log("lit", that.list);
		})
	}
}
